  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.metric:last-child {
  margin-bottom: 0;
}

.metric-label {
  color: var(--text-regular);
  font-size: 14px;
}

.metric-value {
  color: var(--text-primary);
  font-weight: 500;
}

.skill-actions {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

/* È°πÁõÆÂ±ïÁ§∫ */
.projects-awards {
  margin: 40px 0;
}

.projects-grid {
  display: grid;
  gap: 20px;
}

.projects-grid.grid {
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
}

.projects-grid.list {
  grid-template-columns: 1fr;
}

.project-card {
  height: 100%;
  transition: all 0.3s ease;
}

.project-card:hover {
  border-color: var(--secondary-color);
}

.project-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.project-title {
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 600;
  color: var(--text-primary);
}

.project-description {
  color: var(--text-regular);
  font-size: 14px;
  line-height: 1.5;
  margin: 15px 0;
}

.project-tech {
  margin: 15px 0;
}

.tech-label {
  display: block;
  color: var(--text-regular);
  font-size: 13px;
  margin-bottom: 8px;
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 6px;
}

.project-metrics {
  display: flex;
  justify-content: space-between;
  margin: 20px 0;
  padding: 15px;
  background: rgba(0, 0, 0, 0.02);
  border-radius: 8px;
}

.dark-mode .project-metrics {
  background: rgba(255, 255, 255, 0.05);
}

.metric {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

.metric i {
  color: var(--secondary-color);
  font-size: 18px;
}

.project-actions {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  border-top: 1px solid var(--border-color);
  padding-top: 15px;
}

/* Â•ñÈ°πÊó∂Èó¥ËΩ¥ */
.awards-timeline {
  padding: 20px;
}

.award-card {
  cursor: pointer;
  transition: all 0.3s ease;
  border: none;
}

.award-card:hover {
  transform: translateX(5px);
  box-shadow: 0 6px 16px rgba(0, 0, 0, 0.1) !important;
}

.award-card.national {
  border-left: 4px solid var(--danger-color);
}

.award-card.provincial {
  border-left: 4px solid var(--warning-color);
}

.award-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 15px;
}

.award-icon {
  font-size: 32px;
}

.award-info {
  flex: 1;
}

.award-info h4 {
  margin: 0 0 5px 0;
  color: var(--text-primary);
}

.award-organization {
  color: var(--text-regular);
  font-size: 13px;
  margin: 0;
}

.award-level {
  align-self: flex-start;
}

.award-content {
  margin: 15px 0;
}

.award-content p {
  color: var(--text-regular);
  font-size: 14px;
  line-height: 1.5;
  margin-bottom: 10px;
}

.award-skills {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.award-actions {
  text-align: right;
  border-top: 1px solid var(--border-color);
  padding-top: 15px;
}

/* ËÅîÁ≥ªÂå∫Âüü */
.contact-section {
  margin: 40px 0;
}

.contact-card {
  background: linear-gradient(135deg, var(--card-bg) 0%, rgba(160, 82, 45, 0.05) 100%);
  border: none;
  border-radius: 20px;
  padding: 30px;
}

.contact-card h3 {
  color: var(--text-primary);
  font-size: 24px;
  margin-bottom: 10px;
}

.contact-card p {
  color: var(--text-regular);
  font-size: 15px;
  line-height: 1.6;
  margin-bottom: 25px;
}

.contact-methods {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.contact-method {
  flex: 1;
  min-width: 140px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  transition: all 0.3s ease;
}

.contact-method:hover {
  background: rgba(160, 82, 45, 0.1);
  border-color: var(--secondary-color);
  transform: translateY(-3px);
}

.qr-code-section {
  text-align: center;
}

.qr-code-placeholder {
  width: 150px;
  height: 150px;
  background: white;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  margin: 0 auto 15px;
  border: 2px dashed var(--border-color);
}

.qr-code-placeholder i {
  font-size: 48px;
  color: var(--secondary-color);
}

.qr-code-placeholder p {
  color: var(--text-regular);
  font-size: 14px;
  margin: 0;
}

.qr-tip {
  color: var(--text-regular);
  font-size: 12px;
  margin: 0;
}

/* È°µËÑö */
.footer {
  background: var(--card-bg);
  border-top: 1px solid var(--border-color);
  padding: 30px 0;
  margin-top: 40px;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  text-align: center;
}

.footer-links {
  display: flex;
  justify-content: center;
  gap: 30px;
  margin-bottom: 20px;
}

.footer-links a {
  color: var(--text-regular);
  text-decoration: none;
  font-size: 14px;
  transition: color 0.3s ease;
}

.footer-links a:hover {
  color: var(--secondary-color);
}

.copyright {
  color: var(--text-regular);
  font-size: 14px;
  margin: 10px 0;
}

.tech-stack {
  color: var(--text-regular);
  font-size: 12px;
  opacity: 0.7;
  margin: 5px 0;
}

/* È°πÁõÆËØ¶ÊÉÖÂºπÁ™óÊ†∑Âºè */
.core-project-detail {
  max-height: 70vh;
  overflow-y: auto;
  padding-right: 10px;
}

.core-project-detail h3 {
  color: var(--primary-color);
  margin: 20px 0 15px 0;
  padding-bottom: 8px;
  border-bottom: 2px solid var(--border-color);
  display: flex;
  align-items: center;
  gap: 10px;
}

.core-project-detail h3 i {
  color: var(--secondary-color);
}

.core-project-detail h4 {
  color: var(--text-primary);
  margin: 15px 0 10px 0;
  font-size: 16px;
}

.project-overview {
  background: linear-gradient(135deg, rgba(160, 82, 45, 0.05) 0%, rgba(139, 69, 19, 0.02) 100%);
  padding: 20px;
  border-radius: 12px;
  margin-bottom: 20px;
  border-left: 4px solid var(--secondary-color);
}

.overview-text {
  line-height: 1.8;
  color: var(--text-regular);
  font-size: 15px;
  margin-bottom: 20px;
  text-align: justify;
}

.project-badges {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  margin-top: 15px;
}

/* ËÆ°ÁÆóÊú∫ÂçöÂºàÈ°πÁõÆÊ†∑Âºè */
.tech-points {
  margin-top: 10px;
}

.tech-point {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  margin-bottom: 12px;
  padding: 10px;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  transition: all 0.3s ease;
}

.tech-point:hover {
  border-color: var(--secondary-color);
  transform: translateX(3px);
}

.tech-point i {
  color: var(--success-color);
  margin-top: 2px;
}

.achievements-list {
  margin-top: 10px;
}

.achievement-item {
  display: flex;
  align-items: flex-start;
  gap: 10px;
  margin-bottom: 12px;
  padding: 12px;
  background: linear-gradient(135deg, rgba(231, 182, 26, 0.1) 0%, rgba(255, 215, 0, 0.05) 100%);
  border-radius: 8px;
  border-left: 4px solid #ffd700;
}

.achievement-item i {
  color: #e6a23c;
  margin-top: 2px;
}

/* ‰∏™‰∫∫‰ΩúÂìÅÈõÜÈ°πÁõÆÊ†∑Âºè */
.tech-details {
  margin-top: 10px;
}

.tech-section {
  margin-bottom: 20px;
  padding: 15px;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 8px;
}

.tech-section h4 {
  margin-top: 0;
  color: var(--primary-color);
  border-bottom: 1px solid var(--border-color);
  padding-bottom: 8px;
}

.tech-section p {
  margin: 10px 0 0 0;
  line-height: 1.6;
  color: var(--text-regular);
}

.project-challenges {
  margin: 25px 0;
}

.challenges-content {
  padding: 15px;
  background: linear-gradient(135deg, rgba(102, 194, 58, 0.1) 0%, rgba(67, 160, 71, 0.05) 100%);
  border-radius: 8px;
  border-left: 4px solid var(--success-color);
}

.optimization-list {
  margin-top: 10px;
}

.optimization-item {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 10px;
  padding: 10px;
  background: var(--card-bg);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  transition: all 0.3s ease;
}

.optimization-item:hover {
  border-color: var(--info-color);
  transform: translateX(3px);
}

.optimization-item i {
  color: var(--info-color);
}

.optimization-summary {
  margin-top: 15px;
  padding: 12px;
  background: linear-gradient(135deg, rgba(64, 158, 255, 0.1) 0%, rgba(47, 85, 151, 0.05) 100%);
  border-radius: 8px;
  border-left: 4px solid var(--info-color);
  font-style: italic;
  color: var(--text-regular);
}

/* Êñ∞Â¢ûÔºöËØÅ‰π¶Êü•ÁúãÂºπÁ™óÊ†∑Âºè */
.certificate-container {
  max-height: 70vh;
  overflow-y: auto;
  padding-right: 10px;
}

.certificate-image-wrapper {
  position: relative;
  width: 100%;
  min-height: 400px;
  background-color: #f5f5f5;
  border-radius: 8px;
  margin-bottom: 20px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.dark-mode .certificate-image-wrapper {
  background-color: #2d3748;
}

.certificate-image {
  max-width: 100%;
  max-height: 500px;
  object-fit: contain;
  transition: transform 0.3s ease;
}

.certificate-image:hover {
  transform: scale(1.02);
}

.certificate-loading {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.8);
  color: var(--text-primary);
}

.dark-mode .certificate-loading {
  background: rgba(0, 0, 0, 0.8);
  color: var(--text-primary);
}

.certificate-loading i {
  font-size: 40px;
  margin-bottom: 10px;
  color: var(--secondary-color);
}

.no-certificate {
  text-align: center;
  padding: 60px 20px;
  background: linear-gradient(135deg, rgba(160, 82, 45, 0.05) 0%, rgba(139, 69, 19, 0.02) 100%);
  border-radius: 12px;
  border: 2px dashed var(--border-color);
  margin-bottom: 20px;
}

.dark-mode .no-certificate {
  background: linear-gradient(135deg, rgba(160, 82, 45, 0.1) 0%, rgba(139, 69, 19, 0.05) 100%);
}

.no-certificate i {
  font-size: 60px;
  color: var(--secondary-color);
  margin-bottom: 20px;
  display: block;
}

.no-certificate h3 {
  color: var(--text-primary);
  margin-bottom: 10px;
}

.no-certificate p {
  color: var(--text-regular);
  font-size: 14px;
}

.certificate-details {
  margin-top: 20px;
}

.certificate-details >>> .el-descriptions__body {
  background-color: transparent;
}

.certificate-details >>> .el-descriptions__cell {
  padding: 12px 15px;
}

.skill-tags {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.skill-tag {
  margin: 2px;
}

/* ÊªöÂä®Êù°ÁæéÂåñ */
.core-project-detail::-webkit-scrollbar,
.certificate-container::-webkit-scrollbar {
  width: 6px;
}

.core-project-detail::-webkit-scrollbar-track,
.certificate-container::-webkit-scrollbar-track {
  background: var(--bg-color);
  border-radius: 3px;
}

.core-project-detail::-webkit-scrollbar-thumb,
.certificate-container::-webkit-scrollbar-thumb {
  background: var(--border-color);
  border-radius: 3px;
}

.core-project-detail::-webkit-scrollbar-thumb:hover,
.certificate-container::-webkit-scrollbar-thumb:hover {
  background: var(--secondary-color);
}

/* ÊöóËâ≤‰∏ªÈ¢òÈÄÇÈÖç */
.dark-mode .project-overview {
  background: linear-gradient(135deg, rgba(160, 82, 45, 0.1) 0%, rgba(139, 69, 19, 0.05) 100%);
}

.dark-mode .tech-point,
.dark-mode .tech-section,
.dark-mode .optimization-item {
  background: rgba(255, 255, 255, 0.05);
}

.dark-mode .achievement-item {
  background: linear-gradient(135deg, rgba(231, 182, 26, 0.2) 0%, rgba(255, 215, 0, 0.1) 100%);
}

.dark-mode .challenges-content {
  background: linear-gradient(135deg, rgba(102, 194, 58, 0.2) 0%, rgba(67, 160, 71, 0.1) 100%);
}

.dark-mode .optimization-summary {
  background: linear-gradient(135deg, rgba(64, 158, 255, 0.2) 0%, rgba(47, 85, 151, 0.1) 100%);
}

/* ÂìçÂ∫îÂºèËÆæËÆ° */
@media (max-width: 1200px) {
  .core3d-container {
    flex-direction: column;
  }

  .skills-container {
    flex-direction: column;
  }

  .skills-details {
    min-width: 100%;
  }
}

@media (max-width: 768px) {
  .header {
    padding: 0 16px;
  }

  .nav-center {
    display: none;
  }

  .logo-text {
    font-size: 16px;
  }

  .hero-banner {
    padding: 24px;
  }

  .hero-title {
    font-size: 28px;
  }

  .hero-actions {
    flex-direction: column;
  }

  .projects-grid.grid {
    grid-template-columns: 1fr;
  }

  .contact-methods {
    flex-direction: column;
  }

  .contact-method {
    min-width: 100%;
  }

  .certificate-image-wrapper {
    min-height: 300px;
  }

  .certificate-image {
    max-height: 300px;
  }

  .no-certificate {
    padding: 40px 15px;
  }

  .no-certificate i {
    font-size: 40px;
  }
}
</style>



